.testimonial {
    background-color: #a0a4ab10;
    /* height: 80vh; */
    padding-block: 50px;
}
.testimonials {
    display: flex;
    gap: 1rem;
    padding-block: 50px;
    width: 100%;
}
.testimonial .card {
    min-width: 400px;
    width: 400px;
    background-color: var(--bg);
    padding: 25px;
    border-radius: 15px;
}
@media (max-width: 500px) {
    .testimonial .card {
        width: 100%;
        min-width: 100%;
    }
}
.testimonial .card .card-header {
    display: flex;
    height: 50px;
}
.testimonial .card .card-header img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    padding: 5px;
    margin-right: 10px;
}
.testimonial .card .card-header .flex {
    display: flex;
    flex-direction: column;
    font-size: 13px;
    height: 100%;
    justify-content: center;
}
.testimonial .tip {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--Gry);
}

.scroller {
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
}

.scroller .card {
    scroll-snap-align: start end;
}