.projects .hero {
    padding-top: 40px;
    padding-bottom: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* transition: .5s; */
}

.projects .projects-title {
    transition: .5s;
    line-height: 30px;
    color: var(--DarkPrimary);
}

.projects p {
    margin-block: 15px;
    color: var(--Gry);
    width: 75%;
    max-width: 700px;
    text-align: center;
}

.projects .flex {
    display: flex;
    gap: 1rem;
}

.projects h1 {
    transition: .3s;
}

.projects .flex0 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 4rem;
    padding-top: 15px;
    padding-bottom: 25px;
}

.projects .flex0 .TwoCards {
    display: flex;
    gap: 4rem;
}

.projects .flex a {
    justify-content: center;
    width: 175px;
}

.projects .flex1 {
    display: none;
}

.projects-grid {
    padding-block: 60px;
}

.projects-grid .project {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-block: 18px;
}

.projects-grid .project>*:nth-child(2) {
    padding-left: 25px;
}

.projects-grid .project__txt {
    flex: 1;
}

.projects-grid .project__txt p {
    text-align: start;
    width: 100%;
}

.projects-grid .project__img {
    flex: 1;
}

.projects-grid .project__img img {
    width: 100%;
    border-radius: 15px;
}

.CheckProjectButton {
    border: 1.2px solid var(--DarkPrimary);
    padding: 8px 14px;
    border-radius: 5px;
}

.CheckProjectButton:hover {
    color: var(--bg);
    background-color: var(--DarkPrimary);
    -webkit-animation-name: bounceBorder;
    animation-name: bounceBorder;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
}

.CheckProjectButton svg {
    padding-left: 5px;
    transition: .3s;
}

.CheckProjectButton:hover svg {
    color: var(--bg);
    padding-left: 0px;
}

@keyframes bounceBorder {
    0%, 20%, 50%, 80%, 100% {
        padding-right: 14px;
    }

    40% {
        /* transform: translateX(15px); */
        padding-right: 29px;
    }

    60% {
        padding-right: 19px;
    }
}
@media (min-width: 725px) {
    .projects .mid-none {
        display: none;
    }

    .projects .dnone {
        display: block;
    }
}
@media (max-width: 725px) {
    .projects h1 {
        font-size: 24px;
    }

    .projects p {
        font-size: 15.5px;
    }

    .projects .flex * {
        font-size: 14px;
    }

    .CheckProjectButton {
        width: 100%;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .projects .mid-none {
        display: block;
    }

    .projects .dnone {
        display: none;
    }

    .projects-grid {
        padding-top: 0;
    }

    .projects-grid .project {
        flex-direction: column !important;
    }

    .projects-grid .project>*:nth-child(2) {
        padding-left: 0;
    }
}
@media (max-width: 500px) {
    .projects .hero {
        align-items: start;
        text-align: start;
        justify-content: start;
    }

    .projects .projects-title {
        font-size: 12px;
        position: relative;
        top: 4px;
    }

    .projects p {
        text-align: start;
        width: 100%;
        max-width: 100%;
    }

    .projects .flex {
        flex-direction: column;
        width: 100%;
        gap: .5rem;
        justify-content: center;
        align-items: center;
    }

    .projects .flex a:last-child {
        padding: 12px 16px;
        width: 100%;
    }

    .projects .flex0 {
        display: none;
        width: 100%;
        justify-content: center;
    }

    .projects .flex1 {
        display: flex;
        padding-top: 40px;
    }

    .projects .flex0 .TwoCards {
        display: block;
    }

    .projects .flex0 .TwoCards .card {
        padding-block: 1rem;
        display: block;
    }
}