.home .hero .texts * {
    transition: .3s;
}
.home .hero {
    min-height: 500px;
    max-height: 700px;
    height: 80vh;
    display: flex;
    align-items: center;
}
.home .smallPrimaryText {
    transition: .3s;
    color: var(--DarkPrimary);
}
.home .hero .texts h1 {
    color: var(--Text);
    padding-block: .8rem;
}
.home .hero .texts p {
    color: var(--Gry);
    padding-bottom: 1.6rem;
}
.home .hero .texts .flex {
    display: flex;
    gap: 1rem;
}
.home .hero .texts {
    flex: 1;
}
.home .hero .img {
    flex: 1;
    position: relative;
    animation-name: floating;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}
.home .hero .img img {
    width: 100%;
}
.home .hero .img::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    /* padding: 100px; */
    background-image: url('../../assets/images/Icons.png');
    width: 100%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    animation-name: floating;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}
.home .hero .img::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background-image: url('../../assets/images/Effects.png');
    width: 100%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    animation-name: floating;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}
@keyframes floating {
    0% { transform: translate(0, 0px); }
    50%  { transform: translate(0, 15px); }
    100%   { transform: translate(0, -0px); }
}
@media (max-width: 968px) {
    .home .hero .flex {
        flex-direction: column;
    }
    .home .hero .flex a:last-child {
        padding: 0 16px;
    }
}
@media (max-width: 725px) {
    .home .hero .flex {
        flex-direction: row;
    }
    .home .hero {
        max-height: none;
        min-height: auto;
        height: auto;
        margin: 0 !important;
    }
    .home .hero .texts p {
        width: 75%;
    }
    .home .hero {
        margin-block: 5vh;
        padding-block: 5vh;
    }
    .home .hero .img {
        display: none !important;
    }
}
@media (max-width: 500px) {
    .home .hero .texts h4 {
        font-size: 14px;
        padding-bottom: 0;
    }
    .home .hero .texts h1 {
        padding-top: 0;
        font-size: 26px;
    }
    .home .hero .texts p {
        width: 100%;
        font-size: 13px;
    }
    .home .hero .texts .flex {
        flex-direction: column;
        gap: .6rem;
    }
    .home .hero .texts .flex a {
        font-size: 14px;
        justify-content: center;
    }
    .home .hero .texts .flex a.CheckMyServices {
        padding: 0 16px;
    }
}
