.about .hero {
    padding-top: 40px;
    padding-bottom: 80px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* transition: .5s; */
}
.about .About-title {
    transition: .5s;
    line-height: 30px;
    color: var(--DarkPrimary);
}
.about p {
    margin-block: 15px;
    color: var(--Gry);
    width: 75%;
    max-width: 700px;
    text-align: center;
}
.about .flex {
    display: flex;
    gap: 1rem;
}
.about h1 {
    transition: .3s;
}
@media (max-width: 725px) {
    .about h1 {
        font-size: 24px;
    }
    .about p {
        font-size: 15.5px;
    }
    .about .flex * {
        font-size: 14px;
    }
}
@media (max-width: 500px) {
    .about .hero {
        align-items: start;
        text-align: start;
        justify-content: start;
    }
    .about .About-title {
        font-size: 12px;
        position: relative;
        top: 4px;
    }
    .about p {
        text-align: start;
        width: 100%;
        max-width: 100%;
    }
    .about .flex {
        flex-direction: column;
        width: 100%;
        gap: .5rem;
    }
    .about .flex a:last-child {
        padding: 0 16px;
    }
}