@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.theme-light {
  --Primary: #3B71FE;
  --DarkPrimary: #3B71FE;

  --Gry: #A0A4AB;
  --Text: #0E0E2C;

  --White: #FFFFFF;
  --Dark: #262A31;
  --bg: var(--White);
  --bg-alt: rgb(240, 240, 240);
}

.theme-dark {
  --Primary: #3B71FE;
  --DarkPrimary: #FFFFFF;

  --Gry: #A0A4AB;
  --Text: #FFFFFF;

  --White: #FFFFFF;
  --Dark: #262A31;
  --bg: var(--Dark);
  --bg-alt: rgb(40, 40, 40);
}

body {
  background-color: var(--bg);
  color: var(--Text);
  transition: .5s;
}

.container {
  margin: auto;
  max-width: 1140px;
  padding-inline: 15px;
}

@media (max-width: 1150px) {
  .container {
    max-width: 960px;
  }
}

@media (max-width: 968px) {
  .container {
    max-width: 720px;
  }
}

@media (max-width: 725px) {
  .container {
    max-width: 100%;
  }
}

.CheckProjectButton,
.CheckMyServices {
  display: inline-flex;
  align-items: center;
  /* justify-content: center; */
  transition: .5s;
  text-decoration: none;
  color: var(--Text);
  font-weight: 400;
}

.CheckProjectButton svg,
.CheckMyServices svg {
  transition: .3s;
  color: var(--DarkPrimary);
  margin-left: 5px;
}

.CheckProjectButton:hover svg,
.CheckMyServices:hover svg {
  -webkit-animation-name: bounce;
  animation-name: bounce;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateX(0);
  }

  40% {
    transform: translateX(15px);
  }

  60% {
    transform: translateX(5px);
  }
}

.btn {
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  padding: 10px 16px;
  border-radius: 4px;
}

.btn svg {
  margin-left: 8px;
}

.btn-primary {
  background-color: var(--Primary);
  color: var(--White);
}

.wave {
  -webkit-animation: wave-animation 2.5s infinite ease-in-out;
  animation: wave-animation 2.5s infinite ease-in-out;
  color: unset;
  transform: rotate(14deg);
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(14deg);
  }
  20% {
    transform: rotate(-8deg);
  }
  30% {
    transform: rotate(14deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.smallPrimaryText {
  color: var(--DarkPrimary);
}